import { Link, useMatch, useResolvedPath } from "react-router-dom"
import { BsFillPersonFill, BsCollectionFill, BsAward, BsPin } from "react-icons/bs"
import { useState } from "react";
import { StyleSheet } from "react-native";

export default function Navbar()
{
    const [navOpen, setNavOpen] = useState(false);

    return (
    <nav className = "nav">
        <div className="nav-container">
            <Link to="/" className = "site-title">
                <div className="site-title-container">
                    <div className = "site-title-initial">Seow</div>
                    <div className = "site-title-back">SK</div>
                </div>
            </Link>
            <div className="toggle-button" onClick={() => setNavOpen(!navOpen)}>
                <span className="bar"></span>
                <span className="bar"></span>
                <span className="bar"></span>
            </div>
        </div>
        <div className = "expanded" style={{
            top: navOpen ? "0" : "-400%",
            transitionDelay: navOpen ? "0s": "0s",
        }}>
            <ul>
                <CustomLink to="/about" onClick={() => setNavOpen(false)}>
                    <div className="navLinks">
                        <BsFillPersonFill/>
                        <span>About</span>
                    </div>
                </CustomLink>
                <CustomLink to="/skills" onClick={() => setNavOpen(false)}>
                    <div className="navLinks">
                        <BsAward/>
                        <span>Skills</span>
                    </div>
                </CustomLink>
                <CustomLink to="/projects" onClick={() => setNavOpen(false)}>
                    <div className="navLinks">
                        <BsCollectionFill/>
                        <span>Project</span>
                    </div>
                </CustomLink>
                <CustomLink to="/commitments" onClick={() => setNavOpen(false)}>
                    <div className="navLinks">
                        <BsPin/>
                        <span>Commitments</span>
                    </div>
                </CustomLink>
            </ul>
        </div>
    </nav>
    );
}

function CustomLink({to, children, ...props})
{
    //const path = window.location.pathname;
    const resolvedPath = useResolvedPath(to)
    const isActive = useMatch( {path: resolvedPath.pathname, end: true})
    return (
        <li className={isActive ? "active" : ""}>
            <Link to = {to} {...props}>{children}</Link>
        </li>
    )
}