import ReactTypingEffect from 'react-typing-effect';
// import 'react-alice-carousel/lib/alice-carousel.css';
// import AliceCarousel from 'react-alice-carousel';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";

import { StyleSheet } from 'react-native';

export default function Home(){
    var settings = {
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
        adaptiveHeight: true,
        variableWidth: true
    };
    return (
        <div className="HomePage">
            <div className="WelcomeMsgContainer">
                <div className="WelcomeMsgTextContainer">
                    <div className="WelcomeMsg1Container">
                        <div className="WelcomeMsg1_1">
                            Greetings Visitors!
                        </div>
                        <span className="waving"></span>
                    </div>
                    <div className="WelcomeMsg2Container">
                        <div className="WelcomeMsg2_1">
                            Welcome
                        </div>
                        <span>to my site.</span>
                    </div>
                    <div className="WelcomeMsg3Container">
                        <div className="WelcomeMsg3_1">
                            I am
                        </div>
                        <ReactTypingEffect className="RTE1"
                            text={["Seow Sin Kiat", "an Engineer", "a Maker", "a Programmer"]}
                            cursorRenderer={cursor => <div>{cursor}</div>}
                            eraseSpeed={50}
                            eraseDelay={3000}
                            speed={100}
                            displayTextRenderer={(text, i) => {
                                return(
                                    <div>
                                        {text.split('').map((char, keyIndex) => {
                                            const keyIdx = '${keyIndex}';
                                            return(
                                                <span key={keyIdx} >{char}</span>
                                            );
                                        })}
                                    </div>
                                );
                            }}/>
                    </div>
                </div>
                <img className="profileImage" src={require('./img/profileImg.jpg')}/>
            </div>
            <div className="aLine"/>
            <div className="scrolling-wrapper">
                <div className="carouselHL">Highlights</div>
                <Slider {...settings}>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                    <img src="./img/BabyPheonix.png" style={styles.itemPhoto}/>
                </Slider>
            </div> 
            <div className="aLine"/>
            <div className="interestContainer">
                <div className='interestImg'>
                    <img className="intImg" src={require('./img/sorinosk1.png')}/>
                </div>
                <div className='interestMsg'>
                    <div className='interestMsgTitle'>
                        My Interest?
                    </div>
                    <div className='interestMsgText'>
                        <p>My designs and builds are driven by my deep passion for making. To me, a builds outcome doesnt matter as long as it brings a smile to someone's face. I have created electronic toys for kids, teaching kits for my peers, and applications to make life easier for anyone who uses it. I have always felt that in order to be a successful programmer, I need to first understand the hardware it is programmed on. We can only understand why some protocols are made or created if we understand the hardware limitations. This is why I have chosen computer engineering as my field of study: to learn both hardware and software skills.</p>
                        <p>Aside from making, I love nature and believe in leading an active lifestyle through climbing, cycling, and badminton. If you want to chat about programming, robotics, projects, or anything else, dont hesitate to connect with me through LinkedIn!</p>
                    </div>
                </div>
            </div>
        </div>
    );
}

const styles = StyleSheet.create({
    itemPhoto: {
        width: 80,
        height: 80,
        // borderStyle: "solid",
        margin: "10px"
    }
});